<template>
    <div class="home">
      <div class="circle anim-rotate-left">
        <div class="circle-inner anim-rotate-right">
          <div class="circle-inner anim-rotate-left-2"></div>
        </div>
      </div>
      <img id="star" src="./../assets/star.svg" alt="">
      <canvas id="canvas" width="1080" height="768"></canvas>
      <div class="libra-div">
        <button class="libra-btn" @click='about()'></button>
        <div class="libra-buttons-div">
          <button type="button" class="btn-code" name="button" @click='code()'>Code</button>
          <button type="button" class="btn-art" name="button" @click="art()">Art</button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Home',
  data(){
    return{
      vue_canvas: null
    }
  },
  methods:{
    code:function(){
      this.$router.push({ path: '/code' })
    },
    art:function(){
      this.$router.push({ path: '/art' });
    },
    about:function(){
      this.$router.push({ path: '/about' });
    },
    draw_stars:function(ctx){
      const grid_width = 100;
      const grid_height = 100;
      // const space = 20;

      let grid_x_count = Math.ceil(ctx.canvas.width / grid_width);
      let grid_y_count = Math.floor(ctx.canvas.height / grid_height);

      let pos = [];

      for(let i = 0; i < grid_x_count; i++){
        for(let j = 0; j < grid_y_count; j++){
          pos.push({
            x: (Math.random() * ((i+1) * grid_width)) + (i * grid_width),
            y: (Math.random() * ((j+1) * grid_height)) + (j * grid_height),
            // x: (i * grid_width) + space,
            // y: (j * grid_height) + space,
            size: (Math.random() * 60) + 10
          });
        }
      }

      let img = new Image();
      img = document.getElementById("star");
      // img.src = './../assets/star.svg';
      img.onload = function() {
        pos.forEach((item) => {
          ctx.drawImage(img, item.x, item.y, item.size, item.size);
          // ctx.filter = 'blur(1px)';
        });
      };
    }
  },
  mounted:function(){
    var c = document.getElementById("canvas");
    var ctx = c.getContext("2d");
    ctx.canvas.width  = window.innerWidth - 60;
    ctx.canvas.height = window.innerHeight - 60;
    this.vue_canvas = ctx;

    this.draw_stars(ctx);
  },
  components: {
  }
}
</script>
